import React, { useCallback, useEffect, useState } from "react";
import { Segment } from "semantic-ui-react";
import ReactPlayer from 'react-player';
import { getUser } from "../../../../../utils/common";
import "../style.css";
// import WallPostFeedADS from "../component/wallPostFeedADS/WallPostFeedAdS";

let fetchObj = {
  "userId": "",
  "pageNumber": 0,
  "pageLimit": 10,
  "statusValue": "",
  "filterValue": "",
  "search": "",
  "sort": {},
  "timeZone": ""
};

let defaultStateObj = {
  feedRecord: [],
  totalPage: 0,
  totalRecord: 0,
  hashMore: true,
  isLoader: false,
  clipIndex: 0,
  clipSrc: "https://www.youtube.com/watch?v=o0LNoGrExvA",
  clipsData: [
    "https://www.youtube.com/watch?v=NPls-WwTa1w",
    "https://www.youtube.com/watch?v=ziDEKdp7oLQ",
  ],
};

const vidWidth = "auto";
const vidHeight = "100%";

const RightSideFeed = () => {
  const [state, setState] = useState(defaultStateObj);
  let loggedUser = getUser();
  fetchObj.userId = loggedUser._id;

  let clipIndexs = 0;
  useEffect(() => {
    try {
      function callEffect() {
        clipVideo();
      }; callEffect();
    } catch (err) { console.log(err); };
  }, []);

  const clipVideo = useCallback(() => {
    setInterval(() => {
      setState((pre) => ({
        ...pre,
        clipSrc: state.clipsData[clipIndexs],
        clipIndex: clipIndexs += 1,
      }));

      if (state.clipsData.length === clipIndexs) {
        clipIndexs = 0;
      };
    }, 10000);
  }, [state.clipSrc])

  return (
    <>
      <Segment textAlign="center" secondary basic style={{ padding: "0", height: "65%" }}>
        <ReactPlayer
          playing={true}
          muted={true}
          width={vidWidth}
          height={vidHeight}
          url={state.clipSrc}
          controls={false}
          loop
        />
      </Segment>
    </>
  );
};
export default React.memo(RightSideFeed);